<template>
    <div id="moxing">
      <div class="top">
        <slot name="top"></slot>
      </div>
      <hr>
      <div class="centor">
        <slot name="centor"></slot>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    name: 'MoXing',
    props:{
        name:String
    },
    data () {
      return {   
      }
    }
  }
  </script>
  
  
  <style scoped>
    #moxing{
        width: 90%;
        height: 90vh;
        margin: auto;
    }
    #moxing .centor{
      margin-right: 300px;
      width: 90%;
    }
  .top{
    width: 200px;
    height: 50px;
    border-radius: 0 20px 0 0;
    font-size: 25px;
    text-align: center;
    line-height: 50px;
    background-color: rgb(15, 15, 54);
    transform:translate(100px);
    transition: 3s ease;
  }
  .centor{
    transform:translate(100px);
    transition: 3s ease;
    font-size: 28px;
  }
  </style>


